import React from 'react';
import PropTypes from 'prop-types';
import { platform, IOS } from '@vkontakte/vkui';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import PanelHeaderButton from '@vkontakte/vkui/dist/components/PanelHeaderButton/PanelHeaderButton';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import Button from '@vkontakte/vkui/dist/components/Button/Button';

import persik from '../img/persik.png';
import './Persik.css';

const osName = platform();
var hzhz= new Array();
for(let i = 0;i < 10;i++){
    hzhz[i] = Math.random()*50;   
}
var io="Мне не нравится цена";

    const blueBackground = {
  backgroundColor: 'var(--accent)'
};
const Trersik = props => (
    
	<Panel id={props.id}>
		<PanelHeader
			left={<PanelHeaderButton onClick={props.go} data-to="home">
				{osName === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
			</PanelHeaderButton>}
		>
			Ветрина ваших адоптов
		</PanelHeader>
        <div className="Trersik">
		<img className="Picture" src={persik} alt="Persik The Cat"/>
        <h2 className="Price">{(Math.floor(Math.random()*10+8))} руб</h2>
        </div>
        <div className="Trersik">
		<img className="Picture" src={persik} alt="Persik The Cat"/>
        <h2 className="Price">{(Math.floor(Math.random()*10+8))} руб</h2>
        </div>
        <div className="Trersik">
		<img className="Picture" src={persik} alt="Persik The Cat"/>
        <h2 className="Price">{(Math.floor(Math.random()*10+8))} руб</h2>
        </div>
        <div className="Trersik">
		<img className="Picture" src={persik} alt="Persik The Cat"/>
        <h2 className="Price">{(Math.floor(Math.random()*10+8))} руб</h2>
        </div>

            <Button size="xl" level="3" onClick={props.go} data-to="ignore">
					{io}
			</Button>
	</Panel>
);

Trersik.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default Trersik;